.advantages {
  color: #fff;
  &__title {
    margin: 0;
    padding: 0;
    padding-top: 40px;
    padding-left: 15px;
    font-weight: 300;
    font-size: 107px;
    line-height: 138px;
    text-transform: uppercase;
  }
  &__wrapper {
    display: flex;
    margin-top: 40px;
  }
  &__right {
    width: 650px;
    &-list {
      margin: 0;
      padding: 0;
      list-style-type: none;
      &-items {
        padding-left: 100px;
        position: relative;
        &::before {
          content: '';
          position: absolute;
          width: 24px;
          height: 24px;
          left: 60px;
          top: 42px;
          background-image: url('../img/Subtract (1).svg');
        }
        &-title {
          padding: 35px 0;
          font-size: 25px;
          line-height: 34px;
        }
      }
    }
    &-title {
      padding-left: 40px;
      text-transform: uppercase;
      font-size: 29px;
      line-height: 46px;
      padding-left: 100px;
      font-family: 'Neue Machina', sans-serif;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        width: 316px;
        top: 78px;
        left: 94px;
        border: 2px solid #01C213;
      }
    }
  }
  &__left {
    width: 687px;
    &-list {
      margin: 0;
      padding: 0;
      list-style-type: none;
      &-items {
        padding-left: 100px;
        position: relative;
        &::before {
          content: '';
          position: absolute;
          width: 22px;
          height: 24px;
          left: 60px;
          top: 42px;
          background-image: url('../img/Subtract.svg');
        }
      }
    }
    &-title {
      text-transform: uppercase;
      font-size: 29px;
      line-height: 46px;
      padding-left: 100px;
      font-family: 'Neue Machina', sans-serif;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        width: 316px;
        top: 78px;
        left: 94px;
        border: 2px solid #F6347A;
      }
    }
    &-list {
      margin: 0;
      padding: 0;
      list-style-type: none;
      &-items {
        padding-left: 100px;
        &-title {
          margin: 0;
          padding: 35px 0;
          font-family: 'Neue Machina', sans-serif;
          font-size: 25px;
          line-height: 34px;
        }
      }
    }
  }
}