.content {
  &__logo {
    margin-top: 100px;
    margin-left: 21px;
    &-link {
      margin-left: 240px;
      margin-bottom: 40px;
    }
  }
  &__title {
    width: 600px;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
    font-family: 'Neue Machina', sans-serif;
    letter-spacing: 0.22em;
    font-size: 22px;
    line-height: 44px;
    color: #B3B3B3;
    text-transform: uppercase;
    margin-bottom: 0;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      width: 116px;
      left: 242px;
      top: 114px;
      border: 3px solid #01C213;
    }
  }
  &__hush {
    width: 652px;
    margin-top: 4px;
    text-align: center;
  }
}

@media(min-width: 1480px) {
  .content {
    &__hush {
      margin-top: 41px;
    }
    &__title {
      padding-top: 40px;
      padding-bottom: 20px;
      &::before {
        top: 155px;
      }
    }
  }
}