.about {
  &__title {
    padding-left: 15px;
    font-size: 127px;
    letter-spacing: 0.12em;
    font-weight: 300;
    text-transform: uppercase;
    color: #fff;
    font-family: 'Neue Machina', sans-serif;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin: 0;
    padding: 0;
    &-items {
      display: flex;
      width: 700px;
      padding: 23px 0;
      margin-left: 160px;
      &-title {
        order: 0;
        &-wrap {
          margin: 0;
          padding: 0;
          padding-left: 60px;
          color: #fff;
          font-family: 'Neue Machina', sans-serif;
          font-weight: 300;
          font-size: 25px;
          line-height: 34px;
          &--sum {
            padding-left: 23px;
          }
          &--we {
            padding-left: 69px;
          }
        }
      }
      &-cartoon {
        order: -1;
      }
    }
  }
}