.goclub {
  display: flex;
  color: #fff;
  padding: 200px 58px;
  &__title {
    margin: 0;
    padding: 0 20px;
    font-size: 36px;
    line-height: 53px;
    &-pos {
      margin: 0;
    }
  }
  &__join {
    margin-top: -90px;
    font-size: 43px;
    line-height: 61px;
    text-align: center;
    &-title {
      margin: 0;
      padding-bottom: 40px;
    }
  }
}