.membership {
  &__title {
    font-family: 'Neue Machina', sans-serif;
    font-weight: 300;
    font-size: 79px;
    line-height: 103px;
    color: #fff;
    text-transform: uppercase;
    padding-left: 25px;
    &-list {
      display: flex;
      color: #fff;
      list-style-type: none;
      &-items {
        width: 440px;
        &-title {
          padding-top: 30px;
          width: 415px;
          height: 127px;
          border: 2px solid #21D346;
          border-radius: 20px;
          font-size: 35px;
          line-height: 53px;
          text-align: center;
          &--zv {
            position: relative;
            &::before {
              content: '*';
              position: absolute;
              color: #01C213;
              right: 145px;
            }
          }
          &--bess {
            position: relative;
            &::before {
              content: '*';
              position: absolute;
              color: #01C213;
              right: 75px;
            }
          }
          &-description {
            font-size: 26px;
            line-height: 34px;
            &--pos {
              padding: 20px 0;
              padding-left: 50px;
              &--icon {
                position: relative;
                &::before {
                  content: '';
                  position: absolute;
                  width: 24px;
                  height: 24px;
                  left: 0;
                  top: 26px;
                  background-image: url('../img/Subtract (1).svg');
                  background-repeat: no-repeat;
                }
              }
            }
            &--gr {
              color: #21D346;
            }
          }
          &-price {
            font-size: 35px;
            line-height: 53px;
            letter-spacing: 0.01em;
            text-align: center;
          }
        }
      }
    }
    &--col {
      color: #01C213;
    }
  }
}

.unic {
  font-size: 26px;
  line-height: 34px;
  color: #B0B0B0;
  padding-left: 100px;
  position: relative;
  &::before {
    content: '*';
    position: absolute;
    color: #01C213;
    left: 85px;
  }
}