.decisions {
  &__title {
    font-size: 147px;
    line-height: 201px;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    color: #fff;
    font-family: 'Neue Machina', sans-serif;
    padding-left: 45px;
    font-weight: 300;
  }
  &__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    color: #fff;
    text-transform: uppercase;
  }
  &__items {
    display: flex;
    padding: 45px 0;
    &:last-child {
      padding: 0;
    }
    &-text {
      &-description {
        margin: 0;
        padding-left: 130px;
        padding-top: 10px;
        font-size: 28px;
        line-height: 59px;
        &--size {
          padding-left: 60px;
        }
      }
      &-title {
        font-size: 75px;
        line-height: 71px;
        letter-spacing: 0.06em;
        text-transform: uppercase;
        padding-left: 50px;
        margin: 0;
        &--dif {
          color: #01C213;
        }
        &--project {
          padding-left: 125px;
        }
      }
    }
    &-cartoon {
      margin-top: 25px;
      padding-left: 16px;
    }
  }
}