.menu {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 55px 19px 0 21px;
  }
  &__nav-wrap {
    display: flex;
    margin: 0;
    padding: 0;
    list-style-type: none;
    &-items {
      padding: 5px 47px;
      position: relative;
      &:not(:first-child)::before {
        content: '';
        position: absolute;
        width: 6px;
        height: 6px;
        top: 10px;
        left: 0;
        background-color: #07C81A;
        border-radius: 5px;
      }
      &-link {
        color: #fff;
        text-decoration: none;
        &--cl {
          opacity: 0.3;
        }
      }
    }
  }
  &__logo {
    display: flex;
    text-transform: uppercase;
  }
  &__btn-wrapper {
    text-transform: uppercase;
    padding-left: 65px;
    color: #fff;
    outline: none;
    border: none;
  }
  &__btn {
    margin-top: -15px;
    padding-top: 17px;
    width: 273px;
    height: 55px;
    background-color: #7F1CCD;
    border-radius: 12px;
  }
}